:root {
  --bs-heading-color: #424242 !important;
}

body {
  font-family: "Outfit", sans-serif !important;
  font-optical-sizing: auto;
  font-style: normal;
}

h1, h2, h3, h4, h5 {
  font-weight: 700;
}

#back-to-top {
  width: 10rem;
  right: 0;
  left: auto;
  padding-left: 0 !important;
}

.footer-logos {
  max-width: 400px;
}

h2.sticky-top,
h3.sticky-top {
  top: 5.2rem;
  z-index: 100;
}
@media (max-width: 600px) {
  h2.sticky-top,
  h3.sticky-top {
    top: 8rem;
  }
}

.btn-toolbar {
  z-index: 200;
}

.btn-group .btn {
  line-height: 2;
}

.mt-n5 {
  margin-top: -5rem;
}
@media only screen and (max-device-width: 640px) {
  .mt-n5 {
    margin-top: -8rem;
  }
}

h1 {
  margin-bottom: 3rem !important;
}